.terms {
  margin: 78px 0 140px;
  h3 {
    margin-bottom: 52px;
    font-size: 22px;
    color: #1f1f1f;
  }
  .terms-cont {
    font-size: 14px;
    color: #898989;
    line-height: 1.8;
    & > li {
      margin-bottom: 30px;
      a {
        &:hover {
          color: #1f1f1f;
          text-decoration: underline;
        }
      }
    }
    strong {
      display: block;
      margin-bottom: 12px;
      color: #1f1f1f;
    }
    .num-list {
      margin-left: 16px;
      & > li {
        margin-bottom: 10px;
        list-style-type: decimal;
      }
    }
    .depth1 {
      padding-left: 27px;
      margin-bottom: 10px;
    }
    .depth2 {
      padding-left: 8px;
    }
    .depth3 {
      padding-left: 25px;
    }
    .decimal-list-1 {
      counter-reset: depth1;
      & > li {
        position: relative;
        padding-left: 15px;
        text-indent: -10px;
        &::before {
          position: absolute;
          left: -15px;
          counter-increment: depth1;
          content: "(" counter(depth1) ")";
          text-align: right;
          width: 16px;
        }
      }
      .except {
        text-indent: 0;
        padding-left: 6px;
        li {
          padding-left: 5px;
          text-indent: 10px;
        }
      }
    }
    .decimal-list-2 {
      counter-reset: depth2;
      &>li {
        position: relative;
        padding-left: 13px;
        text-indent: 0;
        &::before {
          position: absolute;
          left: -5px;
          counter-increment: depth2;
          content: counter(depth2) ")";
        }
      }
    }
    .dash-list {
      &>li {
        position: relative;
        padding-left: 4px;
        text-indent: 0;
        &::before {
          position: absolute;
          left: -6px;
          content: "-";
        }
      }
    }
    .bul-list {
      margin-left: 2px;
      & > li {
        position: relative;
        padding-left: 8px;
        font-size: 13px;
        &:before {
          content: '';
          position: absolute;
          left: 0;
          top: 10px;
          width: 2px;
          height: 2px;
          background-color: #c1c1c1;
        }
      }
    }
    .table-help {
      text-align: center;
      color: #1f1f1f;
      border-collapse: collapse;
      th {
        font-weight: bold;
        background-color: #d9d9d9;
      }
      th,
      td {
        padding: 0 14px;
        border: 1px solid #1f1f1f;
        vertical-align: middle;
      }
    }
    &.privacy > li > p {
      padding-left: 12px;
    }
  }
  .tab-content > * {
    display: none;
  }
  .tab-content > .active {
    display: block;
  }
}